            .justify-evenly\:#{$state}:#{$state}{
                justify-content: space-evenly;
            }
        }
    }
    @else if $states == ''{
        .#{$size}\:justify-flex-start{
            justify-content: flex-start;
        }
        .#{$size}\:justify-flex-end{
            justify-content: flex-end;
        }
        .#{$size}\:justify-center{
            justify-content: center;
        }
        .#{$size}\:justify-between{
