        }
        .#{$size}\:justify-evenly{
            justify-content: space-evenly;
        }
    }
    @else{
        @each $state in $states {
            .#{$size}\:justify-flex-start\:#{$state}:#{$state}{
                justify-content: flex-start;
            }
            .#{$size}\:justify-flex-end\:#{$state}:#{$state}{
